<template>
	<view class="form-box">
		<!-- 英文 -->
		<view class="form-item-box" @click="handleChoose('en')">
			<view class="item-left">
				<view class="flag-box">
					<image mode="widthFix" src="../../static/img/en.png"></image>
				</view>
				<view class="language-text">English</view>
			</view>
			<view class="check" v-show="lang === 'en'">
				<image mode="widthFix" src="../../static/img/check.png"></image>
			</view>
		</view>
		<!-- 中文 -->
		<view class="form-item-box" style="margin-bottom: 80rpx;" @click="handleChoose('cn')">
			<view class="item-left">
				<view class="flag-box">
					<image mode="widthFix" src="../../static/img/cn.png"></image>
				</view>
				<view class="language-text">中文</view>
			</view>
			<view class="check" v-show="lang === 'cn'">
				<image mode="widthFix" src="../../static/img/check.png"></image>
			</view>
		</view>
		<!-- 按钮 -->
		<u-button type="primary" shape="circle" :ripple="true" @click="handleConfirmed">
			<slot name="confirmText" />
		</u-button>
	</view>
</template>

<script>
	
	export default {
		data() {
			return {
				// 语言
				lang: ''
			}
		},
		mounted() {
			this.lang = uni.getStorageSync('language') ? uni.getStorageSync('language') : 'cn'
		},
		methods: {
			// 选择语言
			handleChoose(lang) {
				this.lang = lang
				if (lang === 'cn') {
					uni.setStorageSync('language', 'cn')
					this.$i18n.locale = 'cn'
				
				} else if (lang === 'en') {
					uni.setStorageSync('language', 'en')
					this.$i18n.locale = 'en'
				}
			},
			// 按下选好了按钮
			handleConfirmed() {
				if (this.lang === 'cn') {
					uni.setStorageSync('language', 'cn')
					this.$i18n.locale = 'cn'
				
				} else if (this.lang === 'en') {
					uni.setStorageSync('language', 'en')
					this.$i18n.locale = 'en'
				}
				this.$emit('languageComfirmed')
			}
		},
		computed: {
			i18n() {
				return this.$t('language')
			}
		}
	}
</script>

